import "./Tutorial.css";
import investmentSvg from "../../../assets/svg/investing.svg";
import Card from "../../Design/Card";

function Tutorial() {
  return (
    <Card>
      <div id="tutorial">
        <div>
        <p>
          Kalkulaka ti vypot, na kolik penz si me pijt pi rznch
          vch investic <strong>dky sloenmu roen.</strong>
        </p>

        <p>
          <strong>Jak vyplnit kalkulaku?</strong>
        </p>

        <ul>
          <li>Me investovat jednorzov i pravideln</li>
          <li>Nebo zkombinovat oboj, co je nejefektivnj</li>
          <li>Akciov trh pin prmrn 68 % ron</li>
          <li>Vyber, jak dlouho chce investovat</li>
          <li>Spotej si, kolik penz me zskat!</li>
        </ul>
        </div>
        <img
          src={investmentSvg}
          height="120px"
          alt="investment"
          id="tutorial__img"
        />
      </div>
    </Card>
  );
}

export default Tutorial;
